<template>
  <div>
    <!-- {{item}} -->
    <a-form class="line-form">
      <a-form-item :label-col="labelCol" :wrapper-col="wrapperCol" label="">
        <input style="width:100%;" :value="item.lineId" class="input-copy-ant line-input-height" disabled />
      </a-form-item>

      <a-form-item class="required" :label-col="labelCol" :wrapper-col="wrapperCol" label="">
        <input class="input-copy-ant-text line-input-height" style="width:100%;" v-model.trim="item.lineName" />
      </a-form-item>

      <a-form-item :label-col="labelCol" :wrapper-col="wrapperCol" label="">
        <input style="width:100%;" :value="item.fromNodeId" class="input-copy-ant line-input-height" disabled />
      </a-form-item>

      <a-form-item :label-col="labelCol" :wrapper-col="wrapperCol" label="">
        <input style="width:100%;" :value="item.toNodeId" class="input-copy-ant line-input-height" disabled />
      </a-form-item>

      <a-form-item :label-col="labelCol" :wrapper-col="wrapperCol" label="">
        <!-- <input style="width:260px;height:60px" class="input-copy-ant-text"/> -->
        <a-textarea v-model="item.conditions" style="width:100%;height:60px" :rows="4" />
      </a-form-item>
    </a-form>
  </div>
</template>
<script>
const RegUrl = /^http[s]?:\/\/[\da-zA-Z][\da-zA-Z.-]*(:\d+)?[\da-zA-Z\/#&\+=%~]?/;
export default {
  props: ["item"],
  data() {
    return {
      labelCol: {
        xs: { span: 10 },
        sm: { span: 4 }
      },
      wrapperCol: {
        xs: { span: 20 },
        sm: { span: 20 }
      }
    };
  },

  methods: {
    validItem() {
      if (this.item.lineNode) {
        this.$message.error("");
        return false;
      }
      if (this.item.conditions && !RegUrl.test(this.item.conditions)) {
        this.$message.error("");
        return false;
      }
      return this.item;
    }
  }
};
</script>
<style scoped>
.required >>> .ant-form-item-label::before{
  content:'* ';
  font-weight:bold;
  color:#ff0036;
}

</style>

