//
/** 

    
    

    
         - 
         - 
        
                 
    
    
process
    1.create canvas dom tree
    2.add click event
        add rect
        delect rect
        choose rect
        add line:connect rect by line when click one rect to another
        delect line:clear rects connection
    3.add listen mouse event
        mouse move:
            judge is mouse move or move element
            mouse move:
            move element:
 */
import Functions from './methods/functions'
export default class Canvas extends Functions {
    constructor({
        // id
        id = 'Canvas',
        //  @clickthis   onclick
        This = window,
        //   
        dialog = false,
        style = {},
        // 
        methodsName = {},
        // 
        methods = {
            dbClick: null,
            beforeDelete: null
        },
        // 
        options = {},
        // 
        rects = [],
        lines = [],
    }) {
        super()
        let defaultStyle = {
            width: 1000,
            height: 800,
            cssText: null,
            rectWidth:100,
            rectHeight:100,
            rectColor: '#000',
            rectlineWidth: 1,
            lineColor: '#808080',
            chooseColor: '#808080',
            fillColor: '#fff',
            lineWidth: 1,
            lineCap: 'round',
            lineJoin: 'round',
            fontColor: '#808080',
            textBaseline: 'middle',
            textAlign: "center",
            font: '12px arial'
        },
            defaultOptions = {
                type: "2d",
                // 
                useBtn: true,
                // 
                clickEmptyInit:true,
                //  
                btnState: {
                    choose: true,
                    delete: true,
                    connect: true,
                },
                // 
                connectRule: {
                    // 
                    alertWarn: false,
                    // 
                    fromEnd: false,
                    // 
                    toStart: false,
                    //  return boolean
                    from: () => { return true },
                    //   return boolean
                    to: () => { return true }
                },
                // id
                addId: null,
                // 
                dragLine: true,
                // 
                edit: true,
                // 
                frame: 60,
                // 
                flex: 6,
                // 
                deleteStartEnd: false,
                // Id
                startId: 1,
                // id 
                endId: 999999999,
                // title
                title: '',
                // text
                text: '',
            }, defaultMethods = {
                add: 'add',
                delete: 'del',
                connect: 'connect',
                choose: 'choose'
            }
        Object.assign(defaultStyle, style)
        Object.assign(defaultOptions, options)
        Object.assign(defaultMethods, methodsName)
        Object.assign(this, {
            id,
            This,
            dialog,
            style: defaultStyle,
            options: defaultOptions,
            methodsName: defaultMethods,
            methods,
            rects,
            lines,
            DOM: document.getElementById(id),
            chooseItem: null,
            /** 
             * dragging 
             * resizeToTop
             * resizeToTopLeft
             * resizeToTopRight
             * resizeToLeft
             * resizeToRight
             * resizeToBottom
             * resizeToBottomLeft
             * resizeToBottomRight
             */
            control: null,
            isDragging: false,
            dragAdd: false,
            _lastTime: 0,
            _startXY: [0, 0],
            // 
            lineFrom: null, // [fromId,toId] null 
            btnState: 'choose',
            rectId: 1,
            lineId: lines[0] ? Math.max(...lines.map(v => v.id)) + 1 : 1
        })
        this.rectId = rects[0] ? Math.max(...rects.map(v => v.id).filter(v => v != this.options.endId)) + 1 : 1,
            this.init()
    }
}

