<template>
  <div class="rect-modal-box-outer">
    <a-form layout="inline" class="rect-modal-box">
      <div class="form-one-line">
        <a-form-item
          colon
          label=""
          :label-col="formItemLayout.labelCol"
          :wrapper-col="formItemLayout.wrapperCol"
          style="flex:1"
        >
          <a-input
            v-model="item.nodeId"
            class="input-copy-ant input-height"
            disabled
            style="width:100%;"
          />
        </a-form-item>

        <a-form-item
          label=""
          :label-col="formItemLayout.labelCol"
          :wrapper-col="formItemLayout.wrapperCol"
          style="flex:1"
        >
          <a-input
            v-model="item.nodeName"
            class="input-copy-ant-text input-height"
            style="width:100%;"
          />
        </a-form-item>
      </div>

      <div class="form-one-line">
        <a-form-item
          label=""
          style="flex:1"
          :label-col="formItemLayout.labelCol"
          :wrapper-col="formItemLayout.wrapperCol"
        >
          <a-select v-model="item.nowMode" @change="nowModechange" class="input-height">
            <a-select-option :value="1"></a-select-option>
            <a-select-option :value="2"></a-select-option>
          </a-select>
        </a-form-item>
        <a-form-item
          label=""
          style="flex:1"
          :label-col="formItemLayout.labelCol"
          :wrapper-col="formItemLayout.wrapperCol"
        >
          <a-select v-model="item.nextMode" style="width: 120px" class="input-height">
            <a-select-option :value="1"></a-select-option>
            <a-select-option :value="2" :disabled="item.nowMode ==1?false:true"></a-select-option>
          </a-select>
        </a-form-item>
      </div>

      <div class="form-one-line">
        <a-form-item
          colon
          label="()"
          style="flex:1"
          :label-col="formItemLayout.labelCol"
          :wrapper-col="formItemLayout.wrapperCol"
        >
          <a-input v-model="item.period" type="number" min="0" class="input-height" />
        </a-form-item>

        <a-form-item
          label=""
          style="flex:1"
          :label-col="formItemLayout.labelCol"
          :wrapper-col="formItemLayout.wrapperCol"
        >
          <a-input v-model="item.minUser" type="number" min="0" class="input-height"></a-input>
        </a-form-item>
      </div>

      <div class="form-one-line rejectable">
        <a-form-item
          style="flex:1"
          label=""
          :label-col="formItemLayout.labelCol"
          :wrapper-col="formItemLayout.wrapperCol"
        >
          <a-select
            mode="tags"
            style="width: 100%"
            v-model="tempRejectNode"
            placeholder=""
            :disabled="item.nodeId==1  || item.nodeId==9999? true :false"
            class="input-height"
          >
            <a-select-option
              v-for="item in rejectList"
              :value="item.nodeName"
              :key="String(item.nodeId) "
            >{{item.nodeName}}</a-select-option>
          </a-select>
        </a-form-item>

        <a-form-item
          style="flex:1"
          label=""
          :label-col="formItemLayout.labelCol"
          :wrapper-col="formItemLayout.wrapperCol"
          class="input-height"
        >
          <a-select v-model="item.rejectMode" style="width: 120px">
            <a-select-option :value="1"></a-select-option>
            <a-select-option :value="2"></a-select-option>
            <a-select-option :value="3"></a-select-option>
          </a-select>
        </a-form-item>
      </div>
      <a-form-item
        label=""
        class="Submit-suggestion"
        :label-col="{ span: 4}"
        :wrapper-col="{ span: 10 }"
      >
        <a-input v-model="item.defOpinion" class="input-height"></a-input>
      </a-form-item>
      <br />
      <a-form-item
        label=":"
        class="Submit-suggestion"
        :label-col="{ span: 4}"
        :wrapper-col="{ span: 10 }"
      >
        <a-select
          mode="multiple"
          v-model="item.tempuserIdList"
          placeholder=""
          style="width: 100%"
          :filterOption="false"
          @search="fetchUser"
          @change="handleChange"
          :notFoundContent="fetching ? undefined : null"
        >
          <a-spin v-if="fetching" slot="notFoundContent" size="small" />
          <a-select-option v-for="d in data" :key="d.value">{{d.text}}</a-select-option>
        </a-select>
      </a-form-item>
      <div class="form-one-line">
        <a-form-item
          label=""
          style="flex:1"
          :label-col="formItemLayout.labelCol"
          :wrapper-col="formItemLayout.wrapperCol"
        >
          <a-select v-model="item.decideUser" class="input-height">
            <a-select-option
              :value="v.userId"
              v-for="(v,i) in decideUserList"
              :key="i"
            >{{v.userName}}</a-select-option>
          </a-select>
        </a-form-item>
        <a-form-item
          label=""
          style="flex:1"
          :label-col="formItemLayout.labelCol"
          :wrapper-col="formItemLayout.wrapperCol"
        >
          <a-select v-model="item.nodeType" style="width: 120px" class="input-height">
            <a-select-option :value="3"></a-select-option>
            <a-select-option :value="4"></a-select-option>
            <a-select-option :value="5"></a-select-option>
          </a-select>
        </a-form-item>
      </div>
      <a-form-item
        label=":"
        class="Submit-suggestion"
        :label-col="{ span: 4}"
        :wrapper-col="{ span: 10 }"
      >
        <a-select
          mode="tags"
          style="width: 100%"
          v-model="item.temppostIdList"
          placeholder=""
          :disabled="item.nodeId==9999? true :false"
          class="input-height"
          @change="handleChangePost"
        >
          <a-select-option
            v-for="item in rolelist"
            :value="item.postName"
            :key="String(item.postId) "
          >{{item.postName}}</a-select-option>
        </a-select>
      </a-form-item>
      <br />
      <div style="padding-left: 130px;">
        <a-checkbox-group
          v-model="item.chooseval"
          :disabled="item.nodeId==1 || item.nodeId==9999? true :false"
        >
          <a-checkbox
            v-for="(item,i) in plainOptions"
            :key="i"
            :value="item.value"
            class="filter-check-box"
          >{{item.value}}</a-checkbox>
        </a-checkbox-group>
      </div>
      <br />
      <div class="form-one-line">
        <a-form-item
          label=""
          style="flex:1"
          :label-col="formItemLayout.labelCol"
          :wrapper-col="formItemLayout.wrapperCol"
        >
          <a-select v-model="item.noticeWay" style="width: 120px" class="input-height">
            <a-select-option :value="0"></a-select-option>
            <a-select-option :value="1"></a-select-option>
          </a-select>
        </a-form-item>
        <a-form-item
          label=""
          style="flex:1"
          :label-col="formItemLayout.labelCol"
          :wrapper-col="formItemLayout.wrapperCol"
        >
          <a-select v-model="item.noticeType" style="width: 120px" class="input-height">
            <a-select-option :value="0"></a-select-option>
            <a-select-option :value="1"></a-select-option>
          </a-select>
        </a-form-item>
      </div>
      <div class="form-one-line">
        <a-form-item
          colon
          label="()"
          style="flex:1"
          :label-col="formItemLayout.labelCol"
          :wrapper-col="formItemLayout.wrapperCol"
        >
          <a-input v-model="item.noticeCycle" type="number" min="0" class="input-height" />
        </a-form-item>
        <a-form-item
          label
          style="flex:1"
          :label-col="formItemLayout.labelCol"
          :wrapper-col="formItemLayout.wrapperCol"
        ></a-form-item>
      </div>
      <a-form-item
        label="():"
        class="Submit-suggestion"
        :label-col="{ span: 4}"
        :wrapper-col="{ span: 10 }"
      >
        <a-select
          mode="multiple"
          v-model="item.tempuserIdList"
          placeholder=""
          style="width: 100%"
          :filterOption="false"
          @search="fetchUser"
          @change="handleChange"
          :notFoundContent="fetching ? undefined : null"
        >
          <a-spin v-if="fetching" slot="notFoundContent" size="small" />
          <a-select-option v-for="d in data" :key="d.value">{{d.text}}</a-select-option>
        </a-select>
      </a-form-item>
      <a-form-item
        class="Submit-suggestion"
        label=""
        :label-col="{ span: 4}"
        :wrapper-col="{ span: 10 }"
      >
        <a-textarea v-model="item.arriveFunc"></a-textarea>
      </a-form-item>
      <br />
      <a-form-item
        class="Submit-suggestion"
        label=""
        :label-col="{ span: 4}"
        :wrapper-col="{ span: 10 }"
      >
        <a-textarea v-model="item.submitFunc"></a-textarea>
      </a-form-item>
      <br />
      <a-form-item
        class="Submit-suggestion"
        label=""
        :label-col="{ span: 4 }"
        :wrapper-col="{ span: 10 }"
      >
        <a-textarea v-model="item.rejectFunc"></a-textarea>
      </a-form-item>
    </a-form>
  </div>
</template>
<script>
import jsonp from "fetch-jsonp";
import querystring from "querystring";
import debounce from "lodash/debounce";

import { Promise } from "q";
import {
  getRoleList,
  userList,
  userPostList,
  userListtemp
} from "../../../api/UserApi";
const RegUrl = /^http[s]?:\/\/[\da-zA-Z][\da-zA-Z.-]*(:\d+)?[\da-zA-Z\/#&\+=%~]?/;

export default {
  props: ["item"],
  data() {
    this.lastFetchId = 0;
    this.fetchUser = debounce(this.fetchUser, 800);
    return {
      plainOptions: [
        { label: "reject", value: "" },
        { label: "beReject", value: "" },
        { label: "dynamic", value: "" }
      ],
      // options,
      valueuserIdList: [],
      data: [],
      personvalue: [],
      PositionValue: [],
      fetching: false,
      dataposition: [],
      formItemLayout: {
        labelCol: { span: 9 },
        wrapperCol: { span: 15 }
      },
      roleName: [],
      rolelist: [],
      rejectList: [],
      tempRejectNode: [],
      canvasData: {},
      nowModeDisabled: true,
      userList: [],
      decideUserList: [],
      keyArrlength: 0
    };
  },
  mounted() {},
  methods: {
    // 

    async validItem() {
      let item = this.item,
        res = false;
      if (item.nodeId == "") {
        this.$message.error("error info");
        return res;
      }
      if (item.nodeName == "") {
        this.$message.error("");
        return res;
      }
      if (item.nodeType == "") {
        this.$message.error("");
        return res;
      }

      if (item.nowMode == "") {
        this.$message.error("");
        return res;
      }
      if (item.nextMode == "") {
        this.$message.error("");
        return res;
      }
      if (item.nodeType == "") {
        this.$message.error("");
        return res;
      }
      if (!item.rejectMode) {
        this.$message.error("");
        return res;
      }

      if (this.tempRejectNode) {
        item.rejectNode = this.getRejectList(false);
      } else {
        this.$message.error("error");
        return res;
      }
      if (item.arriveFunc && !RegUrl.test(item.arriveFunc)) {
        this.$message.error("");
        return res;
      }
      if (item.submitFunc && !RegUrl.test(item.submitFunc)) {
        this.$message.error("");
        return res;
      }
      if (item.rejectFunc && !RegUrl.test(item.rejectFunc)) {
        this.$message.error("");
        return res;
      }

      item.postIdList = await this.getRolelistmethod(
        item.temppostIdList,
        false
      );
      item.userIdList = await this.getUserlistmethod(
        item.tempuserIdList,
        false
      );
      this.validatecheckbox();
      return this.item;
    },
    async getRolelistmethod(arr = [], getName = true) {
      try {
        if (getName) {
          // let res = await getRoleList({ access_token: "afsaefsf" });
          let res = await userPostList();
          if (!res.data) return [];
          this.rolelist = res.data;
          return arr.map(v => res.data.find(k => k.postId == v).postName);
        } else {
          return arr.map(v => this.rolelist.find(k => k.postName == v).postId);
        }
      } catch (err) {}
    },
    async getUserlistmethod(arr = [], getName = true) {
      try {
        if (getName) {
          // let res = await getRoleList({ access_token: "afsaefsf" });
          let res = await userListtemp();
          if (!res.data) return [];
          this.userList = res.data;
          return arr.map(v => res.data.find(k => k.userId == v).userName);
        } else {
          return arr.map(v => this.userList.find(k => k.userName == v).userId);
        }
      } catch (err) {}
    },
    getDecideUserList(list = []) {
      let { decideUserList, userList } = this;
      decideUserList = userList.filter(v => list.some(m => m == v.userName));
      if (!decideUserList.some(v => v.userId == this.item.decideUser)) {
        this.item.decideUser = null;
      }
      this.decideUserList = decideUserList;
    },
    /**
     * @method
     * @description 
     */
    getRejectList(show = true) {
      let { lines, rects } = this.canvasData;
      if (show) {
        let id = this.item.id,
          temp = [],
          getForwardNode = id => {
            if (lines.some(v => v.toNodeId == id)) {
              let fromId = lines.find(v => v.toNodeId == id).fromNodeId;
              temp.push({
                nodeId: fromId,
                nodeName: rects.find(v => v.nodeId == fromId).nodeName || null
              });
              getForwardNode(fromId);
            }
          };
        getForwardNode(id);
        this.rejectList = temp;
        if (this.item.rejectNode) {
          this.tempRejectNode = this.item.rejectNode.map(
            v => rects.find(m => m.nodeId == v).nodeName
          );
        } else {
          this.tempRejectNode = [];
        }
      } else {
        return this.tempRejectNode.map(
          v => rects.find(m => m.nodeName == v).nodeId
        );
      }
    },
    // checkbox
    onChangereject(checkedValues) {
      this.item.reject = Number(checkedValues.target.checked);
    },
    onChangebeReject(checkedValues) {
      this.item.beReject = Number(checkedValues.target.checked);
    },
    onChangedynamic(checkedValues) {
      this.item.dynamic = Number(checkedValues.target.checked);
    },
    checkbox(choosenum) {
      let tempval = [];
      choosenum.map(v => {
        if (v.reject == 1) tempval.push(v.name);
        if (v.beReject == 1) tempval.push(v.name);
        if (v.dynamic == 1) tempval.push(v.name);
      });
      return tempval;
    },
    validatecheckbox() {
      let val = [];
      this.item.chooseval.map(v => {
        this.plainOptions.map(k => {
          if (k.value == v) {
            val.push({
              label: k.label,
              choose: 1
            });
          }
        });
      });
      if (val.find(v => v.label == "dynamic")) {
        this.item.dynamic = 1;
      } else {
        this.item.dynamic = 0;
      }

      if (val.find(v => v.label == "reject")) {
        this.item.reject = 1;
      } else {
        this.item.reject = 0;
      }
      if (val.find(v => v.label == "beReject")) {
        this.item.beReject = 1;
      } else {
        this.item.beReject = 0;
      }
    },
    nowModechange(value) {
      if (value == 2) {
        this.nowModeDisabled = true;
      } else {
        this.nowModeDisabled = false;
      }
    },
    fetchUser(value) {
      this.lastFetchId += 1;
      const fetchId = this.lastFetchId;
      this.data = [];
      this.fetching = true;
      userList(value).then(res => {
        let data = res.data;
        data = data.map(v => {
          return {
            text: v.userName,
            value: v.userId
          };
        });
        this.data = data;
        this.fetching = false;
      });
    },
    // 
    async handleChange(value) {
      Object.assign(this, {
        value,
        data: [],
        fetching: false
      });
      this.long = value.length;
      if (this.item.len <= value.length) {
        this.item.len++;
        let len = value.length - 1;
        value = value[value.length - 1];
        this.item.tempuserIdList.splice(len);
        let res = await userListtemp();
        this.keyArr = res.data.find(v => v.userId == value).userName;

        this.item.tempuserIdList.push(this.keyArr);
      } else {
        this.item.len--;
        this.item.tempuserIdList = value;
      }
      this.getDecideUserList(this.item.tempuserIdList);
      // this.item.tempuserIdList = value
    },

    // 
    handleChangePost(value) {}
  }
};
</script>
<style lang="less" scoped>
.w-180 {
  width: 180px;
}
.test {
  span {
    border: 1px solid #ff0036;
  }
  input {
    border: 1px solid purple;
    min-width: 10px;
    display: flex;
  }
}
.select-hand {
  color: red;
}
.dropdown-box {
  color: red;
}
</style>

