import http from '@/utils/AxiosRequest';
let access_token = require('@/utils/Constant').default.requestToken
export default {
    getFlow(id) {
        return http({ url: `/template/view-node/${id}`, params: { access_token } })
    },
    saveFlow(data) {
        return http({ method: 'PATCH', data, url: `/template/view-node/design`, params: { access_token } })
    },
    getNode(wft_id, node_id) {
        return http({ url: `/node`, params: { access_token, wft_id, node_id } })
    },
    putNode(wftId, viewNode, wftProcess) {
        return http({ method: 'PATCH', data: { wftId, viewNode, wftProcess }, url: `/node/add-or-edit`, params: { access_token } })
    },
    delNode(wftId, nodeId, viewNode) {
        return http({ method: 'DELETE', data: { wftId, viewNode, wftProcess: { nodeId } }, url: `/node/delete`, params: { access_token } })
    },
    getLine(wft_id, line_id) {
        return http({ url: `/node-line`, params: { access_token, wft_id, line_id } })
    },
    putLine(wftId, viewNode, wftNodeLine) {
        return http({ method: 'PATCH', data: { wftId, viewNode, wftNodeLine }, url: `/node-line/add-or-edit`, params: { access_token } })
    },
    delLine(wftId, lineId, viewNode) {
        return http({ method: 'DELETE', data: { wftId, wftNodeLine: { lineId }, viewNode }, url: `/node-line/delete`, params: { access_token } })
    },
} 