<template>
  <div style="margin:24px;">
    <header>
      <div class="title"></div>
      <div class="btns">
        <div id="add" class="btn" @click="add()">
          <img src="../../assets/canvas/add.png" />
          
        </div>
        <div class="btn" :class="{'select':canvas.btnState=='connect'}" @click="connect()">
          <img src="../../assets/canvas/arraw.png" />
          
        </div>
        <div class="btn" :class="{'select':canvas.btnState=='choose'}" @click="choose()">
          <img src="../../assets/canvas/select.gif" />
          
        </div>
        <div class="btn" :class="{'select':canvas.btnState=='delete'}" @click="del()">
          <img src="../../assets/canvas/delete.png" />
          
        </div>
        <div class="btn" @click="saveFlow">
          <img src="../../assets/canvas/save.gif" />
          
        </div>
      </div>
    </header>
    <div :id="boxId"></div>
    <!-- @ok="saveNode()" -->
    <a-modal
      ref="flowmodal"
      :width="item.lineId?'500px':'750px'"
      title=""
      :visible="visible"
      okText=""
      @cancel="visible=false"
      @ok="saveNode"
      class="modal-info"
    >
      <rect-info ref="rect" v-if="!item.lineId" :item="item" class="rect-info"></rect-info>
      <line-info ref="line" v-else :item="item" class="line-info" id="line-info"></line-info>
    </a-modal>
  </div>
</template>

<script>
import canvas from "canvas-flowchart";
import rectInfo from "./editinfo/rect";
import lineInfo from "./editinfo/line";
import { Modal } from "ant-design-vue";
import { Promise } from "q";
let {
  getFlow,
  saveFlow,
  getNode,
  getLine,
  putNode,
  delNode,
  putLine,
  delLine
} = require("@/api/flowchart").default;
export default {
  name: "edit",
  components: {
    rectInfo,
    lineInfo
  },
  props: ["wftId"],
  data() {
    return {
      id: null,
      boxId: null,
      canvas: {},
      item: {},
      visible: false
    };
  },
  watch: {
    visible() {
      this.canvas.dialog = this.visible;
    }
  },
  mounted() {
    if (this.$route.name == "edit") {
      this.init(this.$route.query.wft_id);
    } else {
      this.init(this.wftId);
    }
  },
  methods: {
    init(id) {
      this.id = id;
      this.boxId =
        (Math.random() * 10000000).toString(16).substr(0, 4) +
        "-" +
        id +
        new Date().getTime() +
        "-" +
        id +
        Math.random()
          .toString()
          .substr(2, 5) +
        id;
      this.getFlow();
    },
    async dbClick(e) {
      try {
        let method = e.from ? getLine : getNode,
          data = e.from
            ? {
                id: e.id,
                wftId: this.id,
                lineId: e.id,
                lineName: e.lineName,
                fromNodeId: e.from,
                toNodeId: e.to,
                conditions: null
              }
            : {
                id: e.id,
                wftId: this.id,
                nodeId: e.id,
                nodeName: e.text,
                nodeType: e.nodeType || 3,
                nowMode: 2,
                nextMode: 1,
                period: 0,
                reject: 0,
                beReject: 0,
                rejectNode: [],
                rejectMode: 1,
                dynamic: 1,
                decideUser: null,
                noticeWay: 0,
                noticeType: 0,
                noticeTarget:null,
                noticeCycle:0,
                wfmId:null,
                // followUp: 0,
                minUser: 0,
                defOpinion: "",
                submitFunc: null,
                rejectFunc: null,
                arriveFunc: null,
                // nodeRole: [],
                // tempNodeRole: []
                postIdList: [],
                temppostIdList: [],
                userIdList: [],
                tempuserIdList: [],
                len: 0, //
                chooseval: []
              };
        this.item = data;

        this.visible = true;
        let res = await method(this.id, e.id),
          item = Object.assign(data, res.data || {});
        if (!e.from) {
          let choosenum = [];
          choosenum.push({ reject: item.reject, name: "" });
          choosenum.push({ beReject: item.beReject, name: "" });
          choosenum.push({ dynamic: item.dynamic, name: "" });

          item.temppostIdList = await this.$refs.rect.getRolelistmethod(
            item.postIdList || []
          );

          item.tempuserIdList = await this.$refs.rect.getUserlistmethod(
            item.userIdList || []
          );
          this.$refs.rect.getDecideUserList(item.tempuserIdList);
          item.chooseval = this.$refs.rect.checkbox(choosenum || []);
          this.item = item;
          this.$refs.rect.canvasData = this.formatParams();
          this.$refs.rect.getRejectList();
        }
        this.item.len = this.item.userIdList ? this.item.userIdList.length : 0;
      } catch (err) {
        throw err;
      }
    },
    async saveNode() {
      try {
        let e = this.item;
        let data = await this.$refs[e.lineId ? "line" : "rect"].validItem();

        if (!data) return;
        let method = putNode,
          index = -1,
          { rects, lines } = this.formatParams();
        if (e.lineId) {
          method = putLine;
          index = lines.findIndex(v => v.lineId == e.lineId);
          lines[index].lineName = e.lineName;
        } else {
          index = rects.findIndex(v => v.nodeId == e.nodeId);
          rects[index].nodeName = data.nodeName;
        }

        let res = await method(this.id, { rect: rects, line: lines }, data);
        this.getFlow();
        this.$message.success("");
        this.visible = false;
      } catch (err) {
        throw err;
      }
    },
    deleteNode(e) {
      return new Promise((resolve, reject) => {
        Modal.confirm({
          centered: true,
          title: "",
          content: "",
          okText: "",
          onOk: async () => {
            try {
              let method = e.from ? delLine : delNode,
                { rects, lines } = this.formatParams();
              if (e.from) {
                lines = lines.filter(v => v.lineId != e.id);
              } else {
                rects = rects.filter(v => v.nodeId != e.id);
                lines = lines.filter(
                  v => v.fromNodeId != e.id && v.toNodeId != e.id
                );
              }
              let res = await method(this.id, e.id, {
                rect: rects,
                line: lines
              });
              resolve();
            } catch (err) {
              throw err;
            }
          }
        });
      });
    },
    formatParams(
      rects = this.canvas.rects,
      lines = this.canvas.lines,
      show = false
    ) {
      let rectKey = {
          nodeId: "id",
          nodeName: "text",
          nodeType: "nodeType",
          x: "x",
          y: "y",
          width: "width",
          height: "height"
        },
        lineKey = {
          lineId: "id",
          lineName: "lineName",
          fromNodeId: "from",
          toNodeId: "to"
        },
        format = (arr, rule) => {
          return arr.map(v => {
            let data = {};
            for (let [key, val] of Object.entries(rule)) {
              if (show) {
                data[val] = v[key];
              } else {
                data[key] = v[val];
              }
            }
            if (data.nodeId) {
              data.nodeType = data.nodeType || 3;
              data.nodeName = data.nodeName || v.id;
            }
            if (data.lineId) data.lineName = data.lineName || v.id;
            return data;
          });
        };
      rects = format(rects, rectKey);
      lines = format(lines, lineKey);
      return {
        rects,
        lines
      };
    },
    async getFlow(id = this.id) {
      try {
        //  1 2 3 4 5 
        let res = await getFlow(id),
          // let res={},
          data = {
            id: this.boxId,
            This: this,
            style: {
              width: "1680",
              heigh: "1095",
              lineWidth: 3,
              rectlineWidth: 0.5
            },
            options: {
              useBtn: false,
              addId: "add",
              dragLine: false,
              startId: 1,
              endId: 9999
            },
            methods: {
              dbClick: this.dbClick,
              beforeDelete: this.deleteNode
            }
          },
          initData = {};
        if (res.data) {
          let { line, rect } = res.data;
          initData = this.formatParams(rect, line, true);
        } else {
          initData = {
            rects: [
              {
                x: 200,
                y: 200,
                width: 100,
                height: 100,
                id: 1,
                nodeType: 1,
                title: "",
                text: ""
              },
              {
                x: 500,
                y: 200,
                width: 100,
                height: 100,
                id: 9999,
                nodeType: 2,
                title: "",
                text: ""
              }
            ]
          };
        }
        Object.assign(data, initData);
        this.canvas = new canvas(data);
      } catch (err) {
        throw err;
      }
    },
    async saveFlow() {
      try {
        let { rects, lines } = this.formatParams();
        let res = await saveFlow({
          wftId: this.id,
          viewNode: {
            rect: rects,
            line: lines
          }
        });
        this.$message.success("");
      } catch (err) {
        throw err;
      }
    }
  }
};
</script>
<style lang="less" scoped>
header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-width: 800px;
  height: 50px;
  border-bottom: 1px solid #aaa;
  .title {
    font-weight: bold;
    font-size: 20px;
  }
  .btns {
    display: flex;
    justify-content: space-around;
    margin: 0px;
    background-color: rgb(255, 255, 255);
    width: 450px;
    padding: 3px;
    font-size: 12px;
    .btn {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 3px;
      width: 80px;
      cursor: pointer;
      img {
        width: 16px;
        margin-right: 10px;
      }
    }
    .btn:hover {
      background: #ccc;
    }
    .select {
      background: #ccc;
    }
  }
}

#box {
  overflow: hidden;
}
</style>
